import React, { FC, useEffect, useRef } from "react";
import { PolyImgProps } from "./Polygon.types";
import { Svg, Path } from "../Svg";
import { useNgon } from "./Polygon.methods";
import { rad } from "../../geometry";

export const PolyImg: FC<PolyImgProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fill = '#000',
	stroke = 'currentColor',
	strokeWidth = 0.1,
	image,
	imageScaleFactor = 1
}) => {
	const [points, viewBox, scaledStrokeWidth, setScale] = useNgon(sides, scale, rad(rotation), cornerRadius, strokeWidth);
	const ref = useRef(null);
	/*
	useEffect(()=>{
		const elems = (ref.current! as SVGGElement).children
		if (elems.length === 0 || elems[0].tagName !== 'svg') return; //do nothing because I wasnt trained for this
		const elem = elems[0];
		elem.removeAttribute('width');
		elem.removeAttribute('height');
		const vb = (elem.getAttribute('viewBox') ?? '0 0 0 0').split(' ').map(v=>parseFloat(v));
	})*/
	return (<Svg 
	viewBox={viewBox}
	width='100%'
	height='100%'>
		<mask id="mask">
			<Path {...{points, stroke, strokeWidth: scaledStrokeWidth}}/>
		</mask>
		<image x="0" y="0" width={viewBox[2]} height={viewBox[3]} xlinkHref={image} mask="url(#mask)"/>
	</Svg>)
}